.RegiserPage {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100vh;

}

.Form {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.FormInputs {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 24px;
}

.RegiserPageFooter {
    display: flex;
    align-items: center;
    gap: 8px;
}

.Login {
    color: #4c4cff;
    transition: all 300ms ease-in-out;
}

.Login:hover {
    color: #ffffff;
}